.application {
  background-color: #283645;
  color: #ffffff;
}


.application__btn {
  display: flex;
  width: 281px;
  height: 55px;
  justify-content: center;
  align-items: center;
  margin-top: 28px;
  margin-left: 19px;
  margin-right: 20px;
  margin-bottom: 36px;
  color: #ffffff;
  font-weight: bold;
  font-size: 18px;
  line-height: 18px;
  border: solid 3px #ffffff;
  border-radius: 28px;
}


.application__btn:hover {
  color: #d22856;
  background-color: #ffffff;
}


.application__btn:active {
  color: #f1becc;
  background-color: #ffffff;
}


.application__info {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 45px;
  margin-right: 22px;
  padding-bottom: 26px;
}


.application__image-background {
  background-image: url(../img/bg-back-mobile@1x.jpg);
  background-color: #283645;
  height: 396px;
  border-top: 1px solid rgba(255, 255, 255, 0.15);

  @media
  (min-resolution: @retina-dpi),
  (min-resolution: @retina-dppx) {
    background-image: url(../img/bg-back-mobile@2x.jpg);
    background-size: cover;
  }
}


.application__image {
  margin-top: 109px;
}


.application__list {
  display: flex;
  align-items: center;
  align-content: space-between;
}


.application__item {
  margin-left: 17px;
  margin-right: 18px;
}


.application__slogan {
  display: none;
}

.application__pic {
  display: none;
}


@media (min-width: @tablet-width) {
  .application {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background-image: url(../img/bg-back-tablet@1x.jpg);
    background-position: center;
    height: 619px;
    margin-bottom: 40px;

    @media
  (min-resolution: @retina-dpi),
  (min-resolution: @retina-dppx) {
    background-image: url(../img/bg-back-tablet@2x.jpg);
    background-size: cover;
    }
  }

  .application__pic {
    display: block;
  }

  .application_pic-img {
    position: relative;
    margin-top: 73px;
  }

  .application__list {
    position: absolute;
    justify-content: flex-start;
    margin-left: 27px;
    margin-top: 367px;
   }

 .application__item {
  margin-right: 35px;
  margin-left: 0;
  }

.application__info {
  position: absolute;
  justify-content: flex-start;
  font-size: 18px;
  line-height: 24px;
  margin-right: 317px;
  margin-left: 22px;
  margin-top: 12px;
  margin-top: 455px;
  }

  .application__image-background {
    display: none;
  }

  .application__slogan {
    position: absolute;
    display: flex;
    margin-top: 86px;
    margin-left: 77px;
    margin-bottom: 39px;
    font-size: 40px;
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
  }

  .application__title {
    margin-top: 0px;
    margin-bottom: 0px;
    font-size: 40px;
    font-weight: 400;
    text-align: center;
  }

  .application__btn {
    position: absolute;
    margin-top: 265px;
    width: 300px;
  }
}


@media (min-width: @desktop-width) {
  .application {
    background-image: url(../img/bg-back-desktop@1x.jpg);
    height: 742px;

    @media
  (min-resolution: @retina-dpi),
  (min-resolution: @retina-dppx) {
    background-image: url(../img/bg-back-desktop@2x.jpg);
    }
  }

  .application_pic-img {
    width: 1042px;
    height: 645px;
    margin-left: 151px;
    margin-top: 99px;
  }

    .application__title {
    margin-top: 20px;
    margin-bottom: 0px;
    margin-left: 92px;
    font-size: 50px;
    font-weight: 300;
  }

  .application__btn {
    margin-top: 342px;
    margin-left: 131px;
  }

  .application__list {
    margin-top: 467px;
    margin-left: 138px;
  }

  .application__slogan {
    margin-top: 86px;
    margin-left: 68px;
  }

  .application__info {
    margin-top: 576px;
    margin-left: 133px;
    margin-right: 704px;
  }
}
